<template>
  <form @submit.prevent="submit">
    <!-- Endereço Residencial -->
    <div class="card my-3">
      <div class="card-header">
        <h5 class="card-title">Endereço Residencial</h5>
      </div>
      <div class="card-body">
        <div class="form-group">
          <label for="ipt-cep">CEP</label>
          <input id="ipt-cep" type="text" class="form-control" maxlength="8" required v-model="cep" @focusout="buscarCep(false)">
        </div>
        <div class="form-group">
          <label for="ipt-uf">Estado (UF)</label>
          <input id="ipt-uf" type="text" class="form-control" maxlength="2" required v-model="uf">
        </div>
        <div class="form-group">
          <label for="ipt-cidade">Cidade</label>
          <input id="ipt-cidade" type="text" class="form-control" maxlength="40" required v-model="cidade">
        </div>
        <div class="form-group">
          <label for="ipt-bairro">Bairro</label>
          <input id="ipt-bairro" type="text" class="form-control" maxlength="50" required v-model="bairro">
        </div>
        <div class="form-group">
          <label for="ipt-logradouro">Endereço/Logradouro</label>
          <input id="ipt-logradouro" type="text" class="form-control" maxlength="60" required v-model="logradouro">
        </div>
        <div class="form-group">
          <label for="ipt-numero">Número</label>
          <input id="ipt-numero" type="text" class="form-control" maxlength="5" required v-model="numero">
        </div>
        <div class="form-group">
          <label for="ipt-complemento">Complemento</label>
          <input id="ipt-complemento" type="text" class="form-control" maxlength="15" v-model="complemento">
        </div>
      </div>
    </div>
    <!-- Endereço Comercial -->
    <div class="card my-3">
      <div class="card-header">
        <h5 class="card-title">Endereço Comercial</h5>
      </div>
      <div class="card-body">
        <div class="d-flex justify-content-between form-group align-items-center">
          <label for="ipt-inscricao" class="mb-0" style="font-weight: 600">Tenho endereço comercial:</label>
          <div class="custom-control custom-toggle">
            <input type="checkbox" id="ipt-inscricao" class="custom-control-input" v-model="possuiEndComercial">
            <label for="ipt-inscricao" class="custom-control-label">&nbsp;</label>
          </div>
        </div>
        <transition name="scale">
          <div v-if="possuiEndComercial">
            <div class="form-group">
              <label for="ipt-cep2">CEP</label>
              <input id="ipt-cep2" type="text" class="form-control" maxlength="8" required v-model="cep2" @focusout="buscarCep(true)">
            </div>
            <div class="form-group">
              <label for="ipt-uf2">Estado (UF)</label>
              <input id="ipt-uf2" type="text" class="form-control" maxlength="2" required v-model="uf2">
            </div>
            <div class="form-group">
              <label for="ipt-cidade2">Cidade</label>
              <input id="ipt-cidade2" type="text" class="form-control" maxlength="40" required v-model="cidade2">
            </div>
            <div class="form-group">
              <label for="ipt-bairro2">Bairro</label>
              <input id="ipt-bairro2" type="text" class="form-control" maxlength="50" required v-model="bairro2">
            </div>
            <div class="form-group">
              <label for="ipt-logradouro2">Endereço/Logradouro</label>
              <input id="ipt-logradouro2" type="text" class="form-control" maxlength="60" required v-model="logradouro2">
            </div>
            <div class="form-group">
              <label for="ipt-numero2">Número</label>
              <input id="ipt-numero2" type="text" class="form-control" maxlength="5" required v-model="numero2">
            </div>
            <div class="form-group">
              <label for="ipt-complemento2">Complemento</label>
              <input id="ipt-complemento2" type="text" class="form-control" maxlength="15" v-model="complemento2">
            </div>
          </div>
        </transition>
      </div>
    </div>
    <!-- Endereço Favorito -->
    <div class="card my-3" v-if="possuiEndComercial">
      <div class="card-header">
        <h5 class="card-title">Endereço Preferencial</h5>
      </div>
      <div class="card-body">
        <p>Qual dos endereços você tem preferência para receber correspondências?</p>
        <div class="custom-control custom-radio">
          <input type="radio" id="ipt-pref1" class="custom-control-input" :value="1" v-model="favorito">
          <label class="custom-control-label" for="ipt-pref1">Endereço Residencial</label>
        </div>
        <div class="custom-control custom-radio">
          <input type="radio" id="ipt-pref2" class="custom-control-input" :value="2" v-model="favorito">
          <label class="custom-control-label" for="ipt-pref2">Endereço Comercial</label>
        </div>
      </div>
    </div>
    <!-- Botoes -->
    <div class="my-3">
      <button type="submit" class="btn btn-block btn-primary">AVANÇAR</button>
      <router-link :to="{name: 'recad-form-documentacao'}" class="btn btn-block btn-secondary">VOLTAR</router-link>
    </div>
  </form>
</template>

<script>
  export default {
    name: "Endereco",
    data: () => ({
      //endereco residencial
      cep: '',
      uf: '',
      cidade: '',
      bairro: '',
      logradouro: '',
      numero: '',
      complemento: '',

      //endereco comercial
      cep2: '',
      uf2: '',
      cidade2: '',
      bairro2: '',
      logradouro2: '',
      numero2: '',
      complemento2: '',

      possuiEndComercial: false,
      favorito: 1
    }),
    created() {
      //Alimentar campos
      if (!sessionStorage.getItem('recad_endereco')) {
        //Carrega da Store
        this.cep = this.$store.state.endereco_residencial.cep;
        this.uf = this.$store.state.endereco_residencial.uf;
        this.cidade = this.$store.state.endereco_residencial.cidade;
        this.bairro = this.$store.state.endereco_residencial.bairro;
        this.logradouro = this.$store.state.endereco_residencial.logradouro;
        this.numero = this.$store.state.endereco_residencial.numero;
        this.complemento = this.$store.state.endereco_residencial.complemento;
        this.possuiEndComercial = !!this.$store.state.endereco_comercial;
        if (this.$store.state.endereco_comercial) {
          this.cep2 = this.$store.state.endereco_comercial.cep;
          this.uf2 = this.$store.state.endereco_comercial.uf;
          this.cidade2 = this.$store.state.endereco_comercial.cidade;
          this.bairro2 = this.$store.state.endereco_comercial.bairro;
          this.logradouro2 = this.$store.state.endereco_comercial.logradouro;
          this.numero2 = this.$store.state.endereco_comercial.numero;
          this.complemento2 = this.$store.state.endereco_comercial.complemento;
          this.favorito = this.$store.state.endereco_favorito;
        }
      } else {
        //Carrega da sessionStorage
        const dados = JSON.parse(sessionStorage.getItem('recad_endereco'));
        this.cep = dados.endereco_residencial.cep;
        this.uf = dados.endereco_residencial.uf;
        this.cidade = dados.endereco_residencial.cidade;
        this.bairro = dados.endereco_residencial.bairro;
        this.logradouro = dados.endereco_residencial.logradouro;
        this.numero = dados.endereco_residencial.numero;
        this.complemento = dados.endereco_residencial.complemento;
        this.possuiEndComercial = !!dados.endereco_comercial;
        if (dados.endereco_comercial) {
          this.cep2 = dados.endereco_comercial.cep;
          this.uf2 = dados.endereco_comercial.uf;
          this.cidade2 = dados.endereco_comercial.cidade;
          this.bairro2 = dados.endereco_comercial.bairro;
          this.logradouro2 = dados.endereco_comercial.logradouro;
          this.numero2 = dados.endereco_comercial.numero;
          this.complemento2 = dados.endereco_comercial.complemento;
          this.favorito = dados.favorito;
        }
      }
    },
    beforeRouteLeave (to, from, next) {
      sessionStorage.setItem('recad_endereco', JSON.stringify({
        endereco_residencial: {
          cep: this.cep,
          uf: this.uf,
          cidade: this.cidade,
          bairro: this.bairro,
          logradouro: this.logradouro,
          numero: this.numero,
          complemento: this.complemento
        },
        endereco_comercial: !this.possuiEndComercial ? null : { //NULL -> não possui endereço comercial
          cep: this.cep2,
          uf: this.uf2,
          cidade: this.cidade2,
          bairro: this.bairro2,
          logradouro: this.logradouro2,
          numero: this.numero2,
          complemento: this.complemento2
        },
        favorito: this.favorito
      }));
      next();
    },
    methods: {
      submit() {
        this.$router.push({name: 'recad-form-contato'});
      },
      async buscarCep(eh_comercial = false) {
        try {
          const cep = eh_comercial ? this.cep2 : this.cep;
          if (cep.length !== 8) throw 'Comprimento do CEP é inválido';
          this.$store.commit('setLoading', 'Consultando CEP');
          const res = await this.$http.axios.get(`https://api.postmon.com.br/v1/cep/${cep}`);
          if (eh_comercial) {
            if (res.data.estado) this.uf2 = res.data.estado;
            if (res.data.cidade) this.cidade2 = res.data.cidade;
            if (res.data.bairro) this.bairro2 = res.data.bairro;
            if (res.data.logradouro) this.logradouro2 = res.data.logradouro;
          } else {
            if (res.data.estado) this.uf = res.data.estado;
            if (res.data.cidade) this.cidade = res.data.cidade;
            if (res.data.bairro) this.bairro = res.data.bairro;
            if (res.data.logradouro) this.logradouro = res.data.logradouro;
          }
        } catch (e) {
          // eslint-disable-next-line no-console
          console.log(e);
        } finally {
          setTimeout(() => this.$store.commit('setLoading', false), 1000);
        }
      }
    },
    watch: {
      possuiEndComercial(newVal) {
        //quando o usuario só possui endereço residencial, não há porque o "favorito" ser !== 1
        if (newVal===false) this.favorito=1;
      }
    }
  }
</script>

<style scoped>

</style>
